import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import { map } from 'rxjs/operators';
import {Observable} from 'rxjs';
import {Reservation} from './reservation.service';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  server='localhost';
  port='8080';
  users:User[]=[]
  user:User;

  constructor(private httpClient:HttpClient) { }
  getUsers(): Observable<any[]> {

    return this.httpClient.get<User[]>('http://' + this.server + ':' + this.port + '/api/user/showall')
  }

  create(user:User):Observable<User>{
    return this.httpClient.post<User>('http://'+this.server+':'+this.port+'/api/user/insert', user)


  }
  delete(user:User): Observable<{}>{
    console.log('delete service')
    return this.httpClient.delete('http://'+this.server+':'+this.port+'/api/user/delete/'+user.id)
  }


  checkUser(userName: string, password: string):Observable<User> {
    return this.httpClient.post<User>('http://' + this.server + ':' + this.port + '/api/user/check', {userName, password});
  }

  getUserByUsername(userName: string): Observable<User> {
    return this.httpClient.post<User>('http://'+this.server+':'+this.port+'/api/user/getuser', userName);


  }
}

export class User{
  id: number;
  userName: string;
  password: string;
  email: string;
  phone: string;
  city: string;

  role: string;
  reservations: Reservation[];

  constructor(id: number, userName: string, password: string, email: string,phone:string , city: string,role:string, reservations:Reservation[]) {
    this.id=id;
    this.userName=userName;
    this.password=password;
    this.email=email;
    this.phone=phone
    this.city=city;

    this.role=role;
    this.reservations=reservations
  }

}
